.title {
  text-align: center;
  color: $dark-blue;
  font-size: 35px;
  line-height: 50px;
  padding-bottom: 25px;
  margin-bottom: 25px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    width: 250px;
    height: 2px;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;

    background-color: $dark-blue;
  }
}
.text {
  color: $dark-blue;
  line-height: 1.2;
  font-size: 18px;
  text-align: center;
  max-width: 730px;
  margin: 0 auto 0px;
}

@include mobile {
  .title {
    padding-bottom: 5px;
    margin-bottom: 10px;
    font-size: 27px;
    &::after {
      width: 100px;
    }
  }
}
